import React from "react";
import { User } from "@cvccorp-components/icons";
import { Paper, Divider, } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import { makeStyles , MuiThemeProvider } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { storiesOf } from "@storybook/react";
import themes from "../../Theme/themes";
import { BrandType } from "../../types/brand";
import { styles } from "../styles";
import TextField from "../TextField";

const useStyles = makeStyles((theme) => ({
    ...styles,
    rootBg: {
        background: theme.palette.background.default,
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
}));

type TextFieldStoryProps = {
    mode: string
    brand: BrandType
    size: "small" | "large"
    label: string
    errorMsg: string
    disabled: boolean
    error: boolean
}

const TextFieldStory = ({
    mode, brand, size, label, errorMsg, disabled, error
}: TextFieldStoryProps) => {
    const classes = useStyles(themes[brand]);

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="TextField">
                <Typography variant="h3">TextField</Typography>
                <br />
                <Divider />
                <br />
                <Grid container direction="row" spacing={2} alignItems="flex-start">
                    <Grid item xs={2}>
                        <TextField
                            classes={classes}
                            size={size}
                            label={label}
                            errorMsg={errorMsg}
                            disabled={disabled}
                            error={error}
                        />
                    </Grid>
                    <Grid item xs={2}>
                        <TextField
                            classes={classes}
                            size={size}
                            label={label}
                            errorMsg={errorMsg}
                            disabled={disabled}
                            error={error}
                            icon={<User size={size} />}
                        />
                    </Grid>
                </Grid>

            </Paper>
        </MuiThemeProvider>

    );
};

const argsControls = {
    args: {
        brand: "CVC",
        mode: "rootBg",
        size: "small",
        label: "Placeholder",
        errorMsg: "Mensagem de erro",
        disabled: false,
        error: false,
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
        },
        mode: {
            type: "select",
            options: ["rootBg", "darkBg"]
        },
        size: {
            options: ["small", "large"],
            control: { type: "radio" }
        },
        label: { type: "string" },
        errorMsg: { type: "string" },
        disabled: { type: "boolean" },
        error: { type: "boolean" }
    }
}

storiesOf("Components/TextField", module)
    .add("Default", (args) => <TextFieldStory {...args}/>, argsControls);
